import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Calendar from '@/components/Calendar'
import Contact from '@/components/Contact'
import OrgHistory from '@/components/OrgHistory'
import Staff from '@/components/Staff'
import Board from '@/components/Board'
import Careers from '@/components/Careers'
import Directions from '@/components/Directions'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/calendar',
      name: 'Calendar',
      component: Calendar
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/history',
      name: 'OrgHistory',
      component: OrgHistory
    },
    {
      path: '/staff',
      name: 'Staff',
      component: Staff
    },
    {
      path: '/board',
      name: 'Board',
      component: Board
    },
    {
      path: '/careers',
      name: 'Careers',
      component: Careers
    },
    {
      path: '/directions',
      name: 'Directions',
      component: Directions
    },
    {
      path: '*',
      name: 'Home',
      component: Home
    }
  ]
})



